import {ThemeProvider, Container, Box, Typography, CardContent} from '@mui/material';

import theme from "../theme";

const Colaboration = () => {

    return(

        <ThemeProvider theme={theme}>
            <Container maxWidth="lg"
                       id='colaboration'
                       sx={{
                           backgroundColor:'transparent',
                           mt:{ xs: 1, md: 10, lg: '20px' },
                           maxHeight:'300px',
                       }}>
                <Box>
                    <CardContent sx={{
                        display:'flex',
                        flexDirection:'column',
                        alignItems:' center'
                    }}>
                        <Typography variant="body2"
                                    sx={{
                                        marginTop: { xs: '20px', md: '10px',lg:'10px', xl:'1px' },
                                        fontSize: { xs: 20, md: 30, xl:30 },
                                        fontWeight: 700,
                                        fontFamily: 'Caveat',
                                        textAlign:'center'
                                    }}>
                           Jeste sadownikiem lub rolnikiem z uprawami kwitncymi? <br/>
                            Chcesz mie wiksze plony?<br/>
                            Skontaktuj si z nasz pasiek wdrown. Wypenij formularz kontaktowy lub zadzwo!
                        </Typography>
                    </CardContent>

                </Box>



            </Container >
        </ThemeProvider >
    )
}

export default Colaboration;